<template>

<div class="d-flex justify-center">
    

          <form id="login-form" @submit.prevent="loginUser">
                     <div class="row">
                       
                       <div class="col-12">
                           <h2 class="text-center">Giriş Yap</h2>
                       </div>
                        
                        <div class="col-12">
                            Email
                            <v-text-field placeholder="Email (İfasoft Kullanıcı Adı@gmail.com)" required type="email" id="email" v-model="email"> </v-text-field>
                        </div>
                          <div class="col-12">
                              Şifre
                            <v-text-field placeholder="Şifre (İfasoft Kullanıcı Adı)" required type="password" id="password" v-model="password"> </v-text-field>
                        </div>

                          <div class="col-12">
              
                            <button v-if="! xhrRequest" class="login-btn">Giriş Yap</button>
                            
                            <button v-if="xhrRequest"  class="login-btn">
                                
    <v-progress-circular
      indeterminate
      color="#DD9D15"
     style="color:white;"
    ></v-progress-circular>
                                Giriş Yapılıyor...
                            </button>
                            
                        </div>
                     </div>
                    
               </form>


    </div>
</template>

<script>
import firebase from 'firebase'

export default{
  data() {
      return {
          email: '',
          password:'',
          xhrRequest:false
      }
  },
  methods: {
      loginUser() {
          let v = this;
 
          v.xhrRequest = true;
          firebase.auth()
          .signInWithEmailAndPassword(this.email, this.password).then(
              () =>{
                  v.xhrRequest = false;
                  
                  this.$router.replace(`kuyular/${this.password}`); 
              },
              (err) => {
                  
                   v.xhrRequest = false;
                  err.message="Kullanıcı Kayıtlı Değil";
                  // console.log(`Error -${err.message}`);
                   alert(err.message);
              }
          );
      }
  },


}
</script>

<style>
 
#login-form{
    width: 100%;
    position: absolute;
    top: 15%;
}



 .login-btn{
     width: 100%;
     background-color: #DD9D15;
     color:white;
     padding: 10px;
     border-radius: 20px;
 }

</style>
