<script>
import axios from "axios";

export default {
  props: ["password"],
  data() {
    return {
      kuyular: [],
      todoIsLoading: true,
    };
  },
 
  computed: {
    filteredKuyular() {
      return this.kuyular.filter(
        (kuyu) => kuyu.kaydeden_kullanici == this.password.toUpperCase()
      );
    },
  },


  created: function () {
    axios
      .get(
        "https://script.googleusercontent.com/macros/echo?user_content_key=chvRZGM5Sc19Aezu9CwQ-HDnxFNpIlCE5J1vo-2ZCGxCuL5eyk2zySraGd1uqWaI9z_VZHgnkFW1NgLshwctLYH_lNnnygRvm5_BxDlH2jW0nuo2oDemN9CCS2h10ox_1xSncGQajx_ryfhECjZEnNNtJQgcKS3QF702zgK-TH4Gp1eoEYE8i140qHugIjx4FfNLxlvVvL0XNnLGxKGAR1LGqL-Wug6q&lib=MFJ8Tf-r-vhO3YkUjbOqvuh0Jelz85Zt4"
      )
      .then((response) => (this.kuyular = response.data.kuyu))
      .finally(() => (this.todoIsLoading = false));
  },
};
</script>

